.downloadPdf {
  color: #fff;
  text-align: center;
  background-color: #4caf50;
  border-radius: 5px;
  padding: 10px 20px;
  font-weight: bold;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.downloadPdf:hover {
  background-color: #45a049;
}

.container {
  flex-direction: row-reverse;
  gap: 10px;
  display: flex;
}

aside nav ul {
  list-style: none;
}

aside nav ul li {
  border: solid none;
  background-color: #d3d3d3;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 5px;
}

aside nav ul li a {
  color: #000;
  text-decoration: none;
}

aside nav ul li a:hover {
  color: #00f;
}

aside {
  width: calc(30% - 10px);
  position: relative;
}

aside nav {
  position: sticky;
}

.sections_wrapper {
  width: calc(70% - 10px);
}

@media screen and (max-width: 768px) {
  aside {
    display: none;
  }

  .sections_wrapper {
    width: 100%;
  }
}

